<template>
  <div>
    <el-alert v-if="!envRight" title="" type="error" show-icon close-text=""
      description="" @close="handleCloseAlert">
    </el-alert>
    <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOutRight">
      <div v-show="show" class="main">
        <div class="btn-field">
          <span class="info">
            <img src="@/assets/images/logo.png" class="logo">
            ()
          </span>
          <el-button type="danger" round @click="dialogVisible = true" class="shangqiang"><i class="el-icon-plus"></i></el-button>
        </div>
        <div class="top-field">
          <div class="bg"></div>
          <img src="@/assets/images/title_bg.png" class="title">
        </div>
        <div v-loading="isLoading">
          <ul class="list">
            <li v-for="(item, index) in list" :key="index" class="item">
              <img src="@/assets/images/xin.png" class="xin">
              <div class="Love_send">To:{{item.to}}</div>
              <img src="@/assets/images/line.png" class="line">
              <div class="Love_text">{{item.content}}</div>
              <div class="Love_from">{{item.from}}</div>
            </li>
          </ul>
        </div>
        <div class="my-pagination" v-show="list.length > 0">
          <el-pagination @current-change="handleCurrentChange" :current-page.sync="page" :page-size="pageSize"
              layout="total, prev, pager, next" :total="total">
          </el-pagination>
        </div>
      </div>
    </transition>

    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.to" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="form.content" placeholder="(><*)"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.from" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="danger" @click="handleSubmitBtnClick"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {getList, addItem} from '@/api'
export default {
  data () {
    return {
      show: false,
      envRight: true,
      from: '',
      to: '',
      content: '',
      list: [],
      page: 1,
      pageSize: 6,
      isLoading: false,
      total: 0,
      dialogVisible: false,
      form: {
        from: '',
        content: '',
        to: ''
      }
    }
  },
  mounted () {
    this.show = true
    if (typeof webExtensionWallet === 'undefined') {
      this.envRight = false
    }
    this.fetchData()
  },
  methods: {
    async fetchData () {
      if (!this.envRight) return
      this.isLoading = true
      const res = await getList([this.page * this.pageSize, this.pageSize])
      this.isLoading = false
      this.list = res.data || []
      this.total = res.total - 6 || 0
      this.list.forEach(item => {
        if (item.from.length > 8) {
          item.from = item.from.substr(0, 8)
        }
      })
    },
    handleCloseAlert () {
      location.href = 'https://github.com/nebulasio/WebExtensionWallet/blob/master/README.md'
    },
    handleCurrentChange (page) {
      console.log(`: ${page}`)
      this.page = page
      this.fetchData()
    },
    async handleSubmitBtnClick () {
      if (!this.envRight) {
        this.$message.error('')
        return
      }
      if (!this.form.to) {
        this.$message.error('()')
        return
      }
      if (!this.form.content) {
        this.$message.error('( ')
        return
      }
      if (!this.form.from) {
        this.$message.error('(')
        return
      }
      const res = await addItem([this.form.from, this.form.to, this.form.content])
      if (res.code === 0) {
        this.$message.success(',1')
        this.dialogVisible = false
        this.form.from = ''
        this.form.to = ''
        this.form.content = ''
      }
    }
  }
}
</script>

<style scoped lang="less">
  .main {
    // min-height: 100vh;
    // box-sizing: border-box;
    background: rgba(255, 255, 255, .9);
    box-shadow: 3px 3px 3px #b4aaaa59;
    // border-bottom-left-radius: 10px;
    // border-bottom-right-radius: 10px;
    width: 980px;
    margin: 0 auto;
    .btn-field {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 0;
      .info {
        color: #e3444c;
        font-size: 18px;
        .logo {
          width: 100px;
          margin: 0 30px;
        }
      }
      .shangqiang {
        margin-right: 30px;
      }
    }
    .top-field {
      position: relative;
      .bg {
        width: 100%;
        height: 10px;
        margin-top: 50px;
        background-image: url('../assets/images/top_bg.png');
      }
      .title {
        position: absolute;
        left: 50%;
        top: -45px;
        margin-left: -150px;
      }
    }
    .list {
      display: flex;
      flex-wrap: wrap;
      background: #ffedb2;
      width: 100%;
      min-height: 500px;
      box-sizing: border-box;
      padding: 20px;
      .item{
        position: relative;
        background: #fff;
        width: 300px;
        height: 220px;
        box-sizing: border-box;
        font-size: 14px;
        color:#e3444c;
        padding: 12px 20px;
        margin-right: 20px;
        box-shadow: 3px 3px 3px #b4aaaa59;
        overflow: hidden;
        &:nth-child(3n) {
            margin-right: 0;
        }
        &:nth-child(n+4) {
            margin-top: 20px;
        }
        .xin {
            position: absolute;
            left: -6px;
            top: -6px;
            width: 36px;
        }
        .line {
            max-width: 300px !important;
            width: 300px;
            margin-left: -20px;
        }
      }  
      .Love_send{
        margin: 5px 0px 0px 20px;
        line-height: 20px;
      }  
      .Love_text{
        margin-top: 25px;
        min-height: 54px;
        max-height: 100px;
        overflow: hidden;
      }  
      .Love_from{
        margin-top: 10px;
        float: right;
      }
  }
  .my-pagination {
    display: flex;
    justify-content: center;
    padding: 20px 0;
  }
  .shangqiang-btn {
    background: #e3444c;
    color: #fff;
    padding: 6px;
    cursor: pointer;
  }
}
</style>



